.header {
   background-color: #131921;
   display: flex;
   align-items: center;
   position: sticky;
    top: 0;
    z-index: 100;
}

.header_optionBasket {
    display: flex;
    align-items: center;
    
}

.header_logo {
    width: 100px;
    object-fit: contain;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 18px;
}


.header_basketCount {
    margin-left: 10px;
    margin-right: 10px;
}
.header_nav {
    display: flex;
    justify-content: space-evenly;
} 

.header_option {
      display: flex;
      flex-direction: column;
      margin-left: 10px;
      margin-right: 10px;
}

.header_optionLineOne {
         font-size: 10px;  
}

.header_link {
        color: white;
        text-decoration: none;
}

.header_search {
    display: flex;
    flex: 1;
}

.header_searchInput {
       height: 12px;
       padding: 10px;
       border: none;
       width: 100%;
}


.header_searchIcon {
    padding: 5px;
    height: 22px !important;
   background-color: #cd9842;
 
}